import React from 'react';
import Types from 'prop-types';

const CompleteStep = props => {
  const {
    isComplete,
    uploadedImage,
    fileName,
    isError,
    isImage,
    errorMessage,
    label,
    cancelText,
    onClear,
  } = props;

  return (
    <div className="droppable-complete-card droppable-card" aria-hidden={!isComplete}>
      <div className="droppable-card-content">
        {!isError ? (
          <div>
            {label && <h4 className="m-b-2">{label}</h4>}{' '}
            {isImage && uploadedImage ? (
              <img src={uploadedImage} alt="OK" className="thumbnail m-b-3" />
            ) : (
              <span className="icon icon-pdf icon-xxl" />
            )}
            {fileName && <p className="text-ellipsis m-b-2">{fileName}</p>}
          </div>
        ) : (
          <div>
            {errorMessage && <h4 className="m-b-2">{errorMessage}</h4>}
            <span className="icon icon-alert icon-xxl text-danger m-b-1" />
          </div>
        )}
        {cancelText && (
          <button className="btn btn-default" onClick={() => onClear()}>
            {cancelText}
          </button>
        )}
      </div>
    </div>
  );
};

CompleteStep.propTypes = {
  isComplete: Types.bool.isRequired,
  uploadedImage: Types.string,
  fileName: Types.string.isRequired,
  isError: Types.bool.isRequired,
  isImage: Types.bool.isRequired,
  errorMessage: Types.string.isRequired,
  label: Types.string.isRequired,
  cancelText: Types.string.isRequired,
  onClear: Types.func.isRequired,
};
CompleteStep.defaultProps = {
  uploadedImage: null,
};

export default CompleteStep;



