import React, { PureComponent } from 'react';
import Types from 'prop-types';

class UploadImageStep extends PureComponent {
  static propTypes = {
    accept: Types.string.isRequired,
    buttonText: Types.string.isRequired,
    disabled: Types.bool.isRequired,
    helpImage: Types.string.isRequired,
    iconUpload: Types.string.isRequired,
    label: Types.string.isRequired,
    placeholder: Types.string.isRequired,
    isComplete: Types.bool.isRequired,
    fileDropped: Types.func.isRequired,
  };

  constructor() {
    super();
    this.uploadInputRef = React.createRef();
  }

  onManualUpload = () => {
    const { fileDropped } = this.props;
    if (this.uploadInputRef && this.uploadInputRef.current) {
      const file = this.uploadInputRef.current.files[0];
      fileDropped(file);
    }
  };

  render() {
    const {
      accept,
      buttonText,
      disabled,
      helpImage,
      iconUpload,
      label,
      placeholder,
      isComplete,
    } = this.props;

    return (
      <div className="droppable-default-card" aria-hidden={isComplete}>
        <div className="droppable-card-content">
          <div className="m-b-2">
            {helpImage && <img src={helpImage} alt={label} className="thumbnail text-xs-center" />}
            {!helpImage && iconUpload && <span className={`icon icon-${iconUpload} icon-xxl`} />}
          </div>
          {label && <h4 className="m-b-1">{label}</h4>}
          {placeholder && <p className="m-b-2">{`${placeholder}`}</p>}
          <label className={`btn btn-primary ${disabled ? 'disabled' : ''}`}>
            {buttonText ? <span>{buttonText}</span> : <span className="icon icon-upload m-r-0" />}

            <input
              type="file"
              accept={accept}
              className="tw-droppable-input hidden"
              disabled={disabled}
              name="file-upload"
              onChange={() => this.onManualUpload()}
              ref={this.uploadInputRef}
              value=""
            />
          </label>
        </div>
      </div>
    );
  }
}

export default UploadImageStep;



