/*eslint-disable */
import React, { Component } from 'react';
import { Upload, Select, Checkbox } from '../src';
import { CompleteStep, ProcessingStep, processingStep } from '../src/upload/steps';

const IMAGES = [
  'data:image/svg+xml;base64,',
  'data:image/jpeg;base64,',
  'data:image/jpeg;base64,',
];
const SIZES = ['sm', 'md', 'lg'];

const TESTHTTPOTIONS = {
  url: 'http://httpbin.org/post',
  method: 'POST',
};

export default class DateLookupDocs extends Component {
  state = {
    animationDelay: 3000,
    buttonText: 'Or Select File',
    cancelText: 'Select other file?',
    disabled: false,
    dropMessage: 'Drop file to start upload',
    failureText: 'Upload failed.Please, try again',
    helpImage: '',
    httpOptions: TESTHTTPOTIONS,
    iconUpload: 'icon icon-upload icon-xxl',
    instructions: 'instructions',
    label: 'Label',
    maxSize: 50000,
    placeholder: 'Drag and drop a file less than',
    processingButtonCancel: 'Cancel',
    processingText: 'Uploading...',
    successText: 'Upload completed!',
    tooLargeMessage: 'tooLargeMessage',
    wrongTypeText: 'wrongTypeText',
  };

  render() {
    return (
      <div className="container">
        <section className="section">
          <div className="row">
            <div className="col-md-6">
              <h2>Upload</h2>
              <p>🤔</p>
              <Upload
                {...this.state}
                onStart={file => console.log('onStart', file)}
                onSuccess={httpResponse => console.log('onSuccess', httpResponse)}
                onFailure={httpResponse => console.log('onFailure', httpResponse)}
                onCancel={() => console.log('onCancel')}
              />
              <div
                className="col-sm-6 droppable droppable-md droppable-processing m-t-2"
                style={{ height: '300px' }}
              >
                <ProcessingStep
                  {...this.state}
                  isError={false}
                  isComplete={false}
                  isProcessing={true}
                  isSuccess={false}
                  onClear={() => {}}
                />
              </div>
              <div
                className="col-sm-6 droppable droppable-md droppable-complete m-t-2"
                style={{ height: '300px' }}
              >
                <CompleteStep
                  {...this.state}
                  isComplete={true}
                  isImage={true}
                  fileName={'image-name.jpg'}
                  uploadedImage={IMAGES[2]}
                  onClear={() => {}}
                  isError={false}
                  errorMessage={'An error Message'}
                />
              </div>
              <div className="row m-t-5">
                <div className="col-sm-12  m-t-5">
                  <pre className="tw-docs-code ">
                    {`<Upload
  buttonText={${this.state.buttonText ? '${this.state.buttonText}' : undefined}}
  cancelText={${this.state.cancelText ? '${this.state.cancelText}' : undefined}}
  disabled={${this.state.disabled ? this.state.disabled : false}}
  failureText={${this.state.failureText ? '${this.state.failureText}' : undefined}}
  iconUpload={${this.state.iconUpload ? '${this.state.iconUpload}' : undefined}}
  label={${this.state.label ? '${this.state.label}' : undefined}}
  onCancel={() => console.log('onCancel')}
  onFailure={httpResponse => console.log('onFailure', httpResponse)}
  onStart={file=>console.log('onStart', (file)}
  onSuccess={httpResponse => console.log('onSuccess', httpResponse)}
  placeholder={${this.state.placeholder ? '${this.state.placeholder}' : undefined}}
  processingText={${this.state.processingText ? '${this.state.processingText}' : undefined}}
  successText={${this.state.successText ? '${this.state.successText}' : undefined}}
  tooLargeMessage={${this.state.tooLargeMessage ? '${this.state.tooLargeMessage}' : undefined}}
  wronTypeMessage={${this.state.wronTypeMessage ? '${this.state.wronTypeMessage}' : undefined}}
  maxSize={${this.state.maxSize ? '${this.state.maxSize}' : undefined}}
  processingButtonCancel={${
    this.state.processingButtonCancel ? '${this.state.processingButtonCancel}' : undefined
  }}
  />`}
                  </pre>
                </div>
              </div>
              {/* eslint-disable react/jsx-indent */}
              {this.getExtraDocs()}
            </div>
            <div className="col-md-6 m-t-5">
              <div className={'row'}>
                <div className="col-md-12">
                  <pre className=" m-t-5 m-b-3"> First Step Props (Image Upload)</pre>
                  <div className="col-md-6 ">
                    <label htmlFor="upload-icon" className="control-label">
                      Icon Upload
                    </label>
                    <input
                      id="upload-icon"
                      type="text"
                      className="form-control"
                      value={this.state.iconUpload}
                      onChange={ev => this.setState({ iconUpload: ev.target.value })}
                    />
                  </div>
                  <div className="col-md-6">
                    <label htmlFor="upload-label" className="control-label">
                      Label
                    </label>
                    <input
                      id="upload-label"
                      type="text"
                      className="form-control"
                      value={this.state.label}
                      onChange={ev => this.setState({ label: ev.target.value })}
                    />
                  </div>
                  <div className="col-md-6 m-t-2">
                    <label htmlFor="upload-helpImage" className="control-label ">
                      Help Image
                    </label>
                    <Select
                      id="date-lookup-size"
                      selected={
                        this.state.helpImage
                          ? { value: this.state.helpImage, label: this.state.helpImage }
                          : undefined
                      }
                      options={IMAGES.map((image, index) => ({
                        value: image,
                        label: `Test image ${index}`,
                      }))}
                      onChange={selection =>
                        this.setState({ helpImage: selection ? selection.value : undefined })
                      }
                    />
                  </div>

                  <div className="col-md-6 m-t-2">
                    <label htmlFor="upload-placeholder" className="control-label">
                      Placeholder
                    </label>
                    <input
                      id="upload-placeholder"
                      type="text"
                      className="form-control"
                      value={this.state.placeholder}
                      onChange={ev => this.setState({ placeholder: ev.target.value })}
                    />
                  </div>
                  <div className="col-md-6 m-t-2">
                    <label htmlFor="upload-button-text" className="control-label">
                      Button text
                    </label>
                    <input
                      id="upload-button-text"
                      type="text"
                      className="form-control"
                      value={this.state.buttonText}
                      onChange={ev => this.setState({ buttonText: ev.target.value })}
                    />
                  </div>
                </div>
              </div>
              <div className={'row'}>
                <div className="col-md-12">
                  <pre className=" m-t-5 m-b-3"> Second Step Props (Processing)</pre>
                  <div className="col-md-6">
                    <label htmlFor="upload-processingText" className="control-label ">
                      Processing Text
                    </label>
                    <input
                      id="upload-processingText"
                      type="text"
                      className="form-control"
                      value={this.state.processingText}
                      onChange={ev => this.setState({ processingText: ev.target.value })}
                    />
                  </div>
                  <div className="col-md-6">
                    <label htmlFor="upload-processing-button-cancel" className="control-label ">
                      Processing Button Text
                    </label>
                    <input
                      id="upload-processing-button-cancel"
                      type="text"
                      className="form-control"
                      value={this.state.processingButtonCancel}
                      onChange={ev => this.setState({ processingButtonCancel: ev.target.value })}
                    />
                  </div>
                  <div className="col-md-6 m-t-2">
                    <label htmlFor="upload-failure-text" className="control-label ">
                      Failure text
                    </label>
                    <input
                      id="upload-failure-text"
                      type="text"
                      className="form-control"
                      value={this.state.failureText}
                      onChange={ev => this.setState({ failureText: ev.target.value })}
                    />
                  </div>
                  <div className="col-md-6 m-t-2">
                    <label htmlFor="upload-successText" className="control-label ">
                      Success Text
                    </label>
                    <input
                      id="upload-successText"
                      type="text"
                      className="form-control"
                      value={this.state.successText}
                      onChange={ev => this.setState({ successText: ev.target.value })}
                    />
                  </div>
                </div>
              </div>
              <div className={'row'}>
                <div className="col-md-12">
                  <pre className=" m-t-5 m-b-3"> Third Step Props (Completed)</pre>
                  <div className="col-md-6 ">
                    <label htmlFor="upload-tooLargeMessage" className="control-label">
                      Too Large Message
                    </label>
                    <input
                      id="upload-tooLargeMessage"
                      type="text"
                      className="form-control"
                      value={this.state.tooLargeMessage}
                      onChange={ev => this.setState({ tooLargeMessage: ev.target.value })}
                    />
                  </div>
                  <div className="col-md-6">
                    <label htmlFor="upload-wrongTypeText" className="control-label">
                      Wrong type Message
                    </label>
                    <input
                      id="upload-wrongTypeText"
                      type="text"
                      className="form-control"
                      value={this.state.wrongTypeText}
                      onChange={ev => this.setState({ wrongTypeText: ev.target.value })}
                    />
                  </div>
                  <div className="col-md-6 m-t-2">
                    <label htmlFor="upload-cancel-text" className="control-label">
                      Button Cancel text
                    </label>
                    <input
                      id="upload-cancel-text"
                      type="text"
                      className="form-control"
                      value={this.state.cancelText}
                      onChange={ev => this.setState({ cancelText: ev.target.value })}
                    />
                  </div>
                </div>
              </div>
              <div className={'row'}>
                <div className="col-md-12">
                  <pre className=" m-t-5 m-b-3"> Shared Props</pre>
                </div>
              </div>

              <div className="col-md-6 ">
                <label htmlFor="date-lookup-size" className="control-label ">
                  Size
                </label>
                <Select
                  id="date-lookup-size"
                  selected={
                    this.state.size ? { value: this.state.size, label: this.state.size } : undefined
                  }
                  options={SIZES.map(size => ({ value: size, label: size }))}
                  onChange={selection =>
                    this.setState({ size: selection ? selection.value : undefined })
                  }
                />
              </div>
              <div className="col-md-6">
                <label htmlFor="upload-disable" className="control-label">
                  Disable
                </label>
                <Checkbox
                  label="Disable"
                  onChange={value => this.setState({ disable: value })}
                  checked={this.state.disable}
                />
              </div>
              <div className="col-md-6">
                <label htmlFor="upload-disable" className="control-label m-t-2">
                  Provide httpOptions?
                </label>
                <Checkbox
                  label="httpOptions"
                  onChange={value => this.setState({ httpOptions: value ? TESTHTTPOTIONS : {} })}
                  checked={Object.keys(this.state.httpOptions).length > 0}
                />
              </div>
              <div className="col-md-6">
                <label htmlFor="upload-max-size" className="control-label m-t-2">
                  Max Size
                </label>
                <input
                  id="upload-max-size"
                  type="text"
                  className="form-control"
                  value={this.state.maxSize}
                  onChange={ev => this.setState({ maxSize: parseInt(ev.target.value) })}
                />
              </div>

              <div className="col-md-6">
                <label htmlFor="upload-max-size" className="control-label m-t-2">
                  Animation Delay
                </label>
                <input
                  id="upload-max-size"
                  type="number"
                  className="form-control"
                  value={this.state.animationDelay}
                  onChange={ev => this.setState({ animationDelay: parseInt(ev.target.value) })}
                />
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
  getExtraDocs = () => (
    <div>
      <p className=" m-t-5">
        Upload is a component for uploading a single file. It is designed to be used multiple times
        when more than one file is required. Files can be saved immediately by passing in a set of
        http-options.
      </p>
      <p>
        The component also contains an HTML file input which can be submitted in a standard
        multipart/form-data form (which requires a name).
      </p>
      <p>
        A number of event handlers are available during the component lifecycle:
        <br />
        <code className="tw-docs-inline-code">
          <strong>on-start(File)</strong>
        </code>
        - fired when we start processing the file, the file object is passed to the handler,
        containing the size, name, and type of the file.
        <br />
        <code className="tw-docs-inline-code">
          <strong>on-success($httpResponse)</strong>
        </code>
        - fired when we successfully process the file and internal animations are complete. The
        success handler will be passed the response from the $http service if http-options were
        supplied.
        <br />
        <code className="tw-docs-inline-code">
          <strong>on-failure($httpResponse)</strong>
        </code>
        - fired when we fail to process the file after internal animations finish. The failure
        handler will be passed the response from the $http service if http-options were supplied. If
        you supply a max-size the component will validate client side and fail with an $httResponse,
        status: '413 - Request Entity Too Large'. For all other errors, interrogate the response and
        supply an appropriate error-message to the component
        <br />
        <code className="tw-docs-inline-code">
          <strong>on-cancel()</strong>
        </code>
        - fired when we reset the control to it's initial state ready to accept a new file. The
        component's post upload screen can be fully customised by transcluding new content to
        replace the default behaviour.
      </p>
    </div>
  );
}



