import React from 'react';
import Types from 'prop-types';
import ProcessIndicator from '../../../processIndicator';
import {
  processIndicatorStatuses,
  processIndicatorSizes,
} from '../../../processIndicator/ProcessIndicator';

const ProcessingStep = props => {
  const {
    processingText,
    successText,
    failureText,
    processingButtonCancel,
    onClear,
    isComplete,
    isProcessing,
    isSuccess,
    isError,
  } = props;

  let statusIndex = 0;

  if (isError) {
    statusIndex = 1;
  }
  if (isSuccess) {
    statusIndex = 2;
  }

  return (
    <div className="droppable-processing-card droppable-card" aria-hidden={isComplete}>
      <div className="droppable-card-content">
        <ProcessIndicator
          size={processIndicatorSizes[1]}
          status={processIndicatorStatuses[statusIndex]}
        />
        <h4 className="m-t-5">
          {!isSuccess && !isError && isProcessing && processingText}
          {isSuccess && successText}
          {isError && failureText}
        </h4>
        {processingButtonCancel && (
          <button className="btn btn-default m-t-2" onClick={() => onClear()}>
            {processingButtonCancel}
          </button>
        )}
      </div>
    </div>
  );
};

ProcessingStep.propTypes = {
  processingText: Types.string.isRequired,
  successText: Types.string.isRequired,
  failureText: Types.string.isRequired,
  processingButtonCancel: Types.string.isRequired,
  onClear: Types.func.isRequired,
  isComplete: Types.bool.isRequired,
  isProcessing: Types.bool.isRequired,
  isSuccess: Types.bool.isRequired,
  isError: Types.bool.isRequired,
};

export default ProcessingStep;



