import React, { PureComponent } from 'react';
import Types from 'prop-types';
import classNames from 'classnames';
import { UploadImageStep, ProcessingStep, CompleteStep } from './steps';
import { postData, asyncFileRead, isSizeValid, generateErrorMessage } from './utils';
import './Upload.less';

class Upload extends PureComponent {
  constructor(props) {
    super(props);
    this.droppableElement = React.createRef();
    this.dragCounter = 0;
    this.state = {
      fileName: '',
      isComplete: false,
      isError: false,
      isProcessing: false,
      isSuccess: false,
      errorMessage: '',
      uploadedImage: null,
      isImage: false,
    };
    this.timeouts = [];
    this.errorMessage = {
      413: props.tooLargeMessage || null,
      415: props.wrongTypeMessage || null,
    };
  }

  componentDidMount() {
    if (this.droppableElement && this.droppableElement.current) {
      this.addDragHandlers(this.droppableElement);
    }
  }

  onDragLeave() {
    this.dragCounter = this.dragCounter - 1;
    if (this.dragCounter === 0) {
      this.setState({ isDroppable: false });
    }
  }
  onDragEnter() {
    this.dragCounter = this.dragCounter + 1;
    const { disabled } = this.props;
    if (this.dragCounter === 1 && !disabled) {
      this.setState({ isDroppable: true });
    }
  }

  asyncPost = file => {
    const formData = new FormData();
    formData.append(file.name, file);
    return postData(this.prepareHttpOptions(this.props.httpOptions), formData);
  };

  asyncFailure = error => {
    const { animationDelay, onFailure } = this.props;
    this.timeouts.push(
      setTimeout(() => {
        this.setState(
          {
            isError: true,
            errorMessage: generateErrorMessage(error.status, this.errorMessage),
          },
          // Gives time to animation to be completed.
          () =>
            this.timeouts.push(
              setTimeout(() => {
                this.setState(
                  {
                    isComplete: true,
                    isProcessing: false,
                  },

                  onFailure(error),
                );
              }, animationDelay),
            ),
        );
      }, animationDelay / 3),
    );

    return error;
  };

  asyncSuccess = response => {
    const { animationDelay, onSuccess } = this.props;
    const { isProcessing } = this.state;
    // If it hasn't been cancelled.
    if (isProcessing) {
      this.timeouts.push(
        setTimeout(() => {
          this.setState(
            {
              isSuccess: true,
            },
            // Gives time to animation to be completed.
            () =>
              this.timeouts.push(
                setTimeout(() => {
                  this.setState(
                    {
                      isProcessing: false,
                      isComplete: true,
                    },
                    onSuccess(response),
                  );
                }, animationDelay),
              ),
          );
        }, animationDelay / 3),
      );
    }

    return response;
  };

  prepareHttpOptions = httpOptions => {
    if (!httpOptions.url) {
      throw new Error('You must supply a URL to post image data asynchronously');
    }

    return httpOptions;
  };

  addDragHandlers($element) {
    $element.current.addEventListener('dragenter', ev => {
      ev.preventDefault();
      this.onDragEnter();
    });

    $element.current.addEventListener('dragover', ev => ev.preventDefault());

    $element.current.addEventListener('dragleave', ev => {
      ev.preventDefault();
      this.onDragLeave();
    });

    $element.current.addEventListener('drop', ev => {
      ev.preventDefault();
      this.fileDropped(ev.dataTransfer.files[0]);
    });
  }

  reset = ev => {
    if (ev) {
      ev.preventDefault();
    }

    this.timeouts.map(timeout => clearTimeout(timeout));
    this.dragCounter = 0;
    const { onCancel } = this.props;

    this.setState(
      {
        isComplete: false,
        isError: false,
        isProcessing: false,
        isSuccess: false,
      },
      onCancel(),
    );
  };

  showDataImage = dataUrl => {
    const { isImage } = this.state;
    if (isImage) {
      this.setState({
        uploadedImage: dataUrl,
      });
    }
  };

  fileDropped = file => {
    const { httpOptions, maxSize, onStart, disabled } = this.props;

    if (disabled) {
      return false;
    }

    if (!file) {
      throw new Error('Could not retrieve file');
    }

    onStart(file);

    this.setState({
      isImage: file.type && file.type.indexOf('image') > -1,
      fileName: file.name,
      isDroppable: false,
      isProcessing: true,
    });

    if (!isSizeValid(file, maxSize)) {
      return this.asyncFailure({
        status: 413,
        statusText: 'Request Entity Too Large',
      });
    }

    // if (!isTypeValid(file, this.accept)) {
    //   this.isWrongType = true;
    //   asyncFailure({
    //     status: 415,
    //     statusText: 'Unsupported Media Type',
    //   });
    //   return;
    // }

    if (httpOptions) {
      // Post the file to provided endpoint
      return this.asyncPost(file)
        .then(response => this.asyncSuccess(response))
        .then(() => asyncFileRead(file))
        .then(response => this.showDataImage(response))
        .catch(error => this.asyncFailure(error));
    }
    // Post on form submit. And return the encoded image.
    return asyncFileRead(file)
      .then(encodedFile => this.asyncSuccess(encodedFile))
      .then(response => this.showDataImage(response))
      .catch(error => this.asyncFailure(error));
  };

  render() {
    const {
      accept,
      buttonText,
      cancelText,
      disabled,
      dropMessage,
      failureText,
      helpImage,
      iconUpload,
      label,
      placeholder,
      processingButtonCancel,
      processingText,
      size,
      successText,
    } = this.props;

    const {
      errorMessage,
      fileName,
      isComplete,
      isDroppable,
      isError,
      isImage,
      isProcessing,
      isSuccess,
      uploadedImage,
    } = this.state;

    return (
      <div
        className={classNames({
          droppable: true,
          'droppable-sm': size === 'sm',
          'droppable-md': size === 'md' || !size,
          'droppable-lg': size === 'lg',
          'droppable-dropping': isDroppable,
          'droppable-processing': isProcessing,
          'droppable-complete': isComplete,
        })}
        ref={this.droppableElement}
      >
        {!isProcessing &&
          !isComplete && (
            <UploadImageStep
              accept={accept}
              buttonText={buttonText}
              disabled={disabled}
              helpImage={helpImage}
              iconUpload={iconUpload}
              label={label}
              placeholder={placeholder}
              isComplete={isComplete}
              fileDropped={file => this.fileDropped(file)}
            />
          )}

        {isProcessing && (
          <ProcessingStep
            isComplete={isComplete}
            isProcessing={isProcessing}
            isSuccess={isSuccess}
            isError={isError}
            processingText={processingText}
            successText={successText}
            failureText={failureText}
            processingButtonCancel={processingButtonCancel}
            onClear={this.reset}
          />
        )}

        {(isSuccess || isError || isComplete) && (
          <CompleteStep
            isComplete={isComplete}
            label={label}
            uploadedImage={uploadedImage}
            fileName={fileName}
            isError={isError}
            isImage={isImage}
            errorMessage={errorMessage}
            cancelText={cancelText}
            onClear={this.reset}
          />
        )}

        {!isProcessing &&
          !isError &&
          !isSuccess &&
          !isComplete && (
            <div className="droppable-dropping-card droppable-card">
              <div className="droppable-card-content">
                <h4 className="m-b-2">{dropMessage}</h4>
                <div className="circle circle-sm">
                  <span className="icon icon-add" />
                </div>
                <p className="m-t-2 m-b-0" />
              </div>
            </div>
          )}
      </div>
    );
  }
}

Upload.propTypes = {
  accept: Types.string,
  animationDelay: Types.number,
  buttonText: Types.string,
  cancelText: Types.string,
  disabled: Types.bool,
  dropMessage: Types.string,
  failureText: Types.string,
  helpImage: Types.string,
  httpOptions: Types.shape({ url: Types.string }),
  iconUpload: Types.string,
  label: Types.string,
  maxSize: Types.number,
  onCancel: Types.func.isRequired,
  onFailure: Types.func.isRequired,
  onStart: Types.func.isRequired,
  onSuccess: Types.func.isRequired,
  placeholder: Types.string,
  processingButtonCancel: Types.string,
  processingText: Types.string,
  size: Types.oneOf(['sm', 'md', 'lg']),
  successText: Types.string,
  tooLargeMessage: Types.string,
  wrongTypeMessage: Types.string,
};

Upload.defaultProps = {
  accept: 'image/*',
  buttonText: 'Or Select File',
  cancelText: 'Select other file?',
  disabled: false,
  dropMessage: 'Drop file to start upload',
  failureText: 'Upload failed.Please, try again',
  helpImage: '',
  httpOptions: null,
  iconUpload: 'icon icon-upload icon-xxl',
  label: '',
  maxSize: 5000000,
  animationDelay: 3000,
  placeholder: 'Drag and drop a file less than',
  processingText: 'Uploading...',
  size: 'md',
  successText: 'Upload completed!',
  tooLargeMessage: 'tooLargeMessage',
  wrongTypeMessage: 'wrongTypeMessage',
  processingButtonCancel: 'Cancel',
};

export default Upload;



